import { WebIOT } from './WebIOT'
export class NFCManager extends WebIOT {
  #nfc = null
  constructor (debug = false, readCb  = (event) => console.log(event), errorCb = (event) => console.log(event)) {
    try {
      super(debug)
    if ('NDEFReader' in window) { /* Scan and write NFC tags */
      this.nfc = new NDEFReader()

      this.nfc.onreading = readCb()
    } else {
      Error('NFC is not supported in your browser')
    }
    } catch (error) {
      alert(error.message)
      
    }
    

  }

  startNFC () {

    this.nfc = new NDEFReader()
  }
  async readNFCData () {
    try {
      if(this.nfc !== null) {
      await this.nfc.scan()
      }
    } catch (e) {
      alert(e.message)
    }
  }
  async writeNFCData (records, errorCb = (event) => console.log(event)) {
    try {
      await this.nfc.write(records)
    } catch (e) {
      errorCb(e)
    }
  }
  async lockNFCTag(errorCb = (event) => console.log(event)) {
    try {
      await this.nfc.makeReadOnly()
    } catch(e) {
      errorCb(e)
    }
  }
  static generateNFC () {
    return new NDEFReader()
  }
}
